
.App {
  text-align: center;
  width: 350px;
  height: 700px;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.frontPageLogo {
  width: 20%;
}
.frontPageButton1 {
  width: 90%;
  margin-top: 20px;
  height: 45px;
  background-color: #722ae8;
  color: white;
  border-radius: 10px;
}

.frontPageButton1:hover {
  background-color: #1d1d1d;
}
.frontPageButton1:disabled {
  background-color: #a9a9a9;
  color: #ffffff;
  cursor: not-allowed;
  opacity: 0.7;
}

.frontPageButton2 {
  width: 90%;
  margin-top: 20px;
  height: 45px;
  background-color: #1d1d1d;
  color: white;
  border-radius: 10px;

}

.frontPageButton2:hover {
  background-color: #722ae8;
}

.frontPageButton3 {
  width: 90%;
  margin-top: 20px;
  height: 45px;
  background-color: #1d1d1d;
  color: white;
  border-radius: 10px;

}

.frontPageButton3:hover {
  background-color: #722ae8;
}

.frontPageBottom {
  align-items: flex-end;
  display: flex;
  height: 100%;
  justify-content: center;
  width: 100%;
  margin-bottom: 15px;
  gap: 5px;
  color: white;
}

.frontPageBottom:hover {
  cursor: pointer;
  color: gray;
}

h2 {
  font-size: 40px;
  margin-bottom: 5px;
  color: rgb(46, 46, 46);
}

.h4 {
  color: rgb(46, 46, 46);
  padding-bottom: 50px;
}

a:visited {
  color: blue;
}

header {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerLogo {
  height: 50px;
  margin-left: 20px;
}

.dropdown {
  margin-right: 20px;
}
.header {
  background-color: #6b53c0;
}
.content {
  text-align: center;
  width: 350px;
  height: 520px;
  overflow-y: auto;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 560px;

  background: linear-gradient(to bottom, #6b53c0 0%, #3e306d 10%, #06050b 20%);
}
.conten{
  text-align: center;
  width: 350px;
  height: 520px;
  overflow-y: auto;
  justify-content: flex-start;
  align-items: center;
  display: flex;
  flex-direction: column;
  position: relative;
}

.mnemonic {
  color: white;
  background: #000000;
  border: 1px solid #722ae8;
  padding: 5px;
  text-align: justify;
  width: 310px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
}

.seedPhraseContainer {
  width: 310px;
  min-height: 160px;
  padding: 5px;
  margin-top: 30px;
  max-width: 100%;
  word-wrap: break-word;
  background-color: #000000;
  color: white;
  border: 1px solid #722ae8;
}
.passwordContainer {
  width: 310px;
  padding: 5px;
  margin-top: 30px;
  max-width: 100%;
  word-wrap: break-word;
  background-color: #000000;
  color: white;
  border: 1px solid #722ae8;
}
.passwordContainer::placeholder {
  color: white;
  opacity: 1;
}
.seedPhraseContainer::placeholder {
  color: white;
  opacity: 1;
}

.walletName {
  font-weight: bold;
  margin-bottom: 5px;
  color: white;
}

.logoutButton {
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  right: 20px;
  top: 5px;
  border-radius: 100px;
}

.logoutButton:hover {
  background-color: rgba(0, 0, 0, 0.036);
  cursor: pointer;
}

.walletView {
  width: 350px;
  padding: 10px;
  height: 100%;
  overflow-y: auto;
}

.sendRow {
  margin-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
}

.nftImage {
  margin-bottom: 10px;
  height: 240px;
  width: 240px;
  object-fit: cover;
  border-radius: 10px;
}
.transaction-history-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.transaction-history-item {
  border-bottom: 1px solid #ddd;
  cursor: pointer;
  padding: 15px;
}

/* .transaction-history-item:hover {
  background-color;
} */

.transaction-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.date-time {
  font-size: 10px;
}
.sent-icon {
  color: red;
  margin-right: 10px;
}

.received-icon {
  color: green;
  margin-right: 10px;
}

.transaction-summary span {
  font-size: 16px;
  margin-right: 20px;
}
.dropdown {
  background-color: #6b53c0;
  border: 1px solid #6b53c0;
}

.transaction-details-dropdown {
  background-color: black;
  padding: 10px;
  margin-top: 10px;
  border-radius: 8px;
  font-size: 14px;
  animation: fadeIn 0.3s ease;
  max-width: 100%;
  word-wrap: break-word;
  border: 1px solid #722ae8;
  color: white;
}

.transaction-details-dropdown p {
  margin: 5px 0;
  overflow-wrap: break-word;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.qrcode {
  margin-top: 2px;
}
.toolTip {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: black;
  border: 1px solid white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 190px;
  text-align: center;
  font-family: "Arial", sans-serif;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
.title {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 2px;
  color: white;
}
.qrcode {
  margin-bottom: 15px;
}

.walletInfo {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  width: 100%;
  color: white;
}

.walletLabel {
  font-size: 0.9rem;
  margin-bottom: 5px;
  color: white;
}

.walletAddress {
  word-wrap: break-word;
  overflow-wrap: break-word;
  font-size: 0.8rem;
  color: white;
  max-width: 100%;
  white-space: normal;
  word-break: break-all;
}

.networkInfo {
  font-size: 0.85rem;
  color: #333;
  margin-top: 10px;
  color: white;
}
.swapRow {
  color: white;
}
.divider {
  color:white ;
}
.input::placeholder {
  color: white;
}
.quoteInfo {
  color: white;
}
